window.$ = window.jQuery = function(selectorOrArrayOrTemplate) {
  let elements;
  if (typeof selectorOrArrayOrTemplate === "string") {
    if (selectorOrArrayOrTemplate[0] === "<") {
      elements = [createElement(selectorOrArrayOrTemplate)];
    } else {
      elements = document.querySelectorAll(selectorOrArrayOrTemplate);
    }
  } else if (selectorOrArrayOrTemplate instanceof Array) {
    elements = selectorOrArrayOrTemplate;
  }

  function createElement(string) {
    const container = document.createElement("template");
    container.innerHTML = string.trim();
    return container.content.firstChild;
  }

  const api = Object.create(jQuery.prototype);
  Object.assign(api, {
    elements: elements, //Âõ†‰∏∫ÊääÂáΩÊï∞ÊîæÂà∞‰∫ÜÂéüÂûãÈáåÔºåÂØºËá¥Ë∞ÉÁî®ÂáΩÊï∞Êó∂Ëé∑Âèñ‰∏çÂà∞elementsÔºå
    //ÊâÄ‰ª•ÊääelementsÂΩì‰ΩúËá™Ë∫´Â±ûÊÄßÊåÇÂà∞ÂØπË±°‰∏äÔºåÁÑ∂ÂêéÂú®ÂéüÂûãÈáåÈÄöËøáthis.elementsËé∑Âèñ
    oldApi: selectorOrArrayOrTemplate.oldApi
  });
  return api;
};

jQuery.fn = jQuery.prototype = {
  constructor: jQuery,
  addClass(className) {
    for (let i = 0; i < this.elements.length; i++) {
      this.elements[i].classList.add(className);
    }
    return this;
  },
  find(selector1) {
    let array = [];
    for (let i = 0; i < this.elements.length; i++) {
      array = array.concat(
        Array.from(this.elements[i].querySelectorAll(selector1))
      );
    }
    array.oldApi = this;
    return jQuery(array);
  },
  appendTo(node) {
    this.each(el => node.appendChild(el));
    return this;
  },
  each(fn) {
    for (let i = 0; i < this.elements.length; i++) {
      fn.call(null, this.elements[i], i);
    }
    return this; //thisÂ∞±ÊòØÂΩìÂâçÁöÑapiÂØπË±°ÔºÅ
  },
  parent() {
    const array = []; //ÂÆπÁ∫≥Áà∏Áà∏
    this.each(node => {
      if (array.indexOf(node.parentNode) === -1) {
        array.push(node.parentNode);
      } //Èò≤Ê≠¢‰∏Ä‰∏™Áà∏Áà∏ÈáåÊúâ‰∏â‰∏™ÂêåÂêçÁöÑÂ≠©Â≠êÔºåËøôÁßçÊÉÖÂÜµ‰∏ãÔºåÂ¶ÇÊûú‰∏çÂÅöÂà§Êñ≠Ôºå‰ºöËøîÂõû‰∏â‰∏™‰∏ÄÊ†∑ÁöÑÁà∏Áà∏
    });
    return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØ‰ª•Êìç‰ΩúËøô‰∫õÁà∏Áà∏ÁöÑÂØπË±°
  },
  children() {
    const array = [];
    this.each(node => {
      array.push(...node.children); //node.childrenÊòØ‰∏Ä‰∏™Êï∞ÁªÑÔºåÊñ∞ËØ≠Ê≥ïÂú®Êï∞ÁªÑÂâçËæπÂä†...ÔºåÊääÊï∞ÁªÑÂ±ïÂºÄÊàêÂÖÉÁ¥†ÁöÑÊÑèÊÄù
    });
    return jQuery(array);
  },
  next() {
    const array = [];
    this.each(node => {
      array.push(node.nextElementSibling);
    });
    return jQuery(array);
  },
  prev() {
    const array = [];
    this.each(node => {
      array.push(node.previousElementSibling);
    });
    return jQuery(array);
  },
  print() {
    console.log(elements); //elementsÂ∞±ÊòØÂΩìÂâçÂØπË±°Êìç‰ΩúÁöÑÂÖÉÁ¥†‰ª¨
  },
  end() {
    return this.oldApi;
  }
};
